import { useExecutionTimeBadge } from '@/composables/node/useExecutionTimeBadge'
import type { NodeExecutionOutput } from '@/schemas/apiSchema'
import { app } from '@/scripts/app'
import { useExtensionService } from '@/services/extensionService'

const EXECUTION_TIME_KEY = '__comfy_execution_time__'

useExtensionService().registerExtension({
  name: 'Comfy.ExecutionTime',
  async nodeCreated(node) {
    // Initialize execution time on node
    ;(node as any)[EXECUTION_TIME_KEY] = 0

    // Ensure node has badges array
    if (!node.badges) {
      node.badges = []
    }

    const executionTimeBadge = useExecutionTimeBadge()

    // Add initial badge showing 0ms
    executionTimeBadge.updateOrAddExecutionTimeBadge(node, 0)

    const onExecuted = node.onExecuted

    // Override onExecuted to capture and store execution time
    node.onExecuted = function (output: NodeExecutionOutput) {
      // Call original onExecuted if it exists
      onExecuted?.call(this, output)

      // Check if output is valid
      if (!output || typeof output !== 'object') {
        return
      }

      const outputObj = output as Record<string, unknown>

      // Debug log
      console.log(
        `[ExecutionTime] Node ${this.id} executed. Output keys:`,
        Object.keys(outputObj)
      )

      // Try to find execution time from various possible fields
      let executionTime: number | undefined

      // Try different possible field names
      if (typeof outputObj.execution_time === 'number') {
        executionTime = outputObj.execution_time
        console.log(`[ExecutionTime] Found execution_time: ${executionTime}ms`)
      } else if (typeof outputObj.exec_time === 'number') {
        executionTime = outputObj.exec_time
        console.log(`[ExecutionTime] Found exec_time: ${executionTime}ms`)
      } else if (typeof outputObj.time === 'number') {
        executionTime = outputObj.time
        console.log(`[ExecutionTime] Found time: ${executionTime}ms`)
      } else {
        // If no time field found, use a placeholder for testing
        // This will be removed once backend integration is complete
        console.log('[ExecutionTime] No execution time found in output')
      }

      if (
        executionTime &&
        typeof executionTime === 'number' &&
        executionTime > 0
      ) {
        ;(this as any)[EXECUTION_TIME_KEY] = executionTime
        executionTimeBadge.updateOrAddExecutionTimeBadge(this, executionTime)

        // Trigger canvas redraw to update badge display
        app.canvas?.setDirty(true, true)
      }
    }
  }
})
